  constructor(private spinner: NgxSpinnerService, private _router:Router, private _service: ServerService,private route: ActivatedRoute) {
    this.route.params.subscribe(event => {
      this.programId = event.program_id
      this.project = localStorage.getItem('project');
    });
  }
  @HostListener('window:popstate', ['$event'])
  onPopState(event) {
    setTimeout(() => {
      let history = localStorage.getItem('history')
      console.log(history)
      if(history==''){
        Swal.fire({
          title: 'Logout',
          text: 'Are you sure you want to logout?',
          icon: 'warning',
          showCancelButton: true,
          confirmButtonText: 'Yes, Logout!',
          cancelButtonText: 'Cancel'
        }).then((result) => {
          if (result.value) {
            this.logout();
          } else if (result.dismiss === Swal.DismissReason.cancel) {

          }
        })
      }
      this._router.navigate([`unitlist`]);
      localStorage.setItem('history','');

    }, 50);

  }

  ngOnInit(): void {
    $(".camp1").addClass('border border-primary');
    $(".camp2").attr("disabled", true);
    $(".camp3").attr("disabled", true);
    $(".camp4").attr("disabled", true);
    $(".con").attr("disabled", true);
    $(".endline").attr("disabled", true);
    
  }

  logout(){
    this._service.logout();
    this._router.navigate([`login`]);
  }

