import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import Swal from 'sweetalert2';
import { ServerService } from '../_services/server.service';
declare var $;

@Component({
  selector: 'app-login-iimpact',
  templateUrl: './login-iimpact.component.html',
  styleUrls: ['./login-iimpact.component.css']
})

export class LoginIimpactComponent implements OnInit {

  loginForm: FormGroup;
  loading = false;
  submitted = false;
  returnUrl: string;
  error = '';

  constructor(private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private ServerService: ServerService) { }

  ngOnInit(): void {
    this.loginForm = this.formBuilder.group({
      email: ['', Validators.required],
      password: ['', Validators.required]
    });
    // get return url from route parameters or default to '/'
    this.returnUrl = '/data-entry/1/1';
    $(".selectpicker").selectpicker('refresh');
  }

  // convenience getter for easy access to form fields
  get f() { return this.loginForm.controls; }


  onSubmit() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.loginForm.invalid) {
        return;
    }

    this.loading = true;

    this.ServerService.login(this.f.email.value, this.f.password.value, 2).then((data)=>{
     console.log(data[0]['user_id']);
      // if (data[0]['is_active'] != 1) {
      //   Swal.fire({
      //     icon: 'error',
      //     title: 'Oops...',
      //     text: 'Incorrect User Name or password!'
      //   })
      // } else {
        //const program = JSON.stringify(data[0]['program']);
        localStorage.setItem('user_id', data[0]['user_id']);
        localStorage.setItem('user', data[0]['user_id']);
        localStorage.setItem('block_name', data[0]['block_name']);
        localStorage.setItem('district_name', data[0]['district_name']);
        localStorage.setItem('state_name', data[0]['state_name']);
        localStorage.setItem('name', data[0]['lc_name']);
        localStorage.setItem('lc_name', data[0]['lc_name']);
        localStorage.setItem('lc_code', data[0]['lc_code']);
        localStorage.setItem('pngo', data[0]['pngo']);
        localStorage.setItem('teacher_name', data[0]['teacher_name']);
        localStorage.setItem('teacher_code', data[0]['teacher_code']);
        localStorage.setItem('donor_name', data[0]['donor_name']);
        localStorage.setItem('supervisor_name', data[0]['supervisor_name']);
        localStorage.setItem('is_tracker', '0');
        localStorage.setItem('history', '1');
        //localStorage.setItem('Program', program);
        localStorage.setItem('project', '2');
        localStorage.setItem('lang', 'en');
        this.router.navigate([this.returnUrl]);
      //}

    }).catch((err)=>{
      console.log(err);
      this.error = err;
      this.loading = false;
    })
  }

}
