import { Component, OnInit, HostListener } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from "ngx-spinner";
import { environment } from 'src/environments/environment';
import Swal from 'sweetalert2';
import { ServerService } from '../_services/server.service'
declare var $;

@Component({
  selector: 'app-create-form',
  templateUrl: './create-form.component.html',
  styleUrls: ['./create-form.component.css']
})

export class CreateFormComponent implements OnInit {

  elements: any = [];
  Program;
  username;
  BlockId;
  programId;
  ProgramData: any = [];
  Action_button: any = [];
  passdata: any
  programName = '';
  project;
  servername;
  is_tracker;

  constructor(private spinner: NgxSpinnerService, private _router:Router, private _service: ServerService,private route: ActivatedRoute) {
    this.route.params.subscribe(event => {
      this.programId = event.program_id
      this.project = localStorage.getItem('project');
    });
  }
  @HostListener('window:popstate', ['$event'])
  onPopState(event) {
    setTimeout(() => {
      let history = localStorage.getItem('history')
      console.log(history)
      if(history==''){
        Swal.fire({
          title: 'Logout',
          text: 'Are you sure you want to logout?',
          icon: 'warning',
          showCancelButton: true,
          confirmButtonText: 'Yes, Logout!',
          cancelButtonText: 'Cancel'
        }).then((result) => {
          if (result.value) {
            this.logout();
          } else if (result.dismiss === Swal.DismissReason.cancel) {

          }
        })
      }
      this._router.navigate([`unitlist`]);
      localStorage.setItem('history','');

    }, 50);

  }

  ngOnInit(): void {
    $(".camp1").addClass('border border-primary');
    $(".camp2").attr("disabled", true);
    $(".camp3").attr("disabled", true);
    $(".camp4").attr("disabled", true);
    $(".con").attr("disabled", true);
    $(".endline").attr("disabled", true);
    
  }

  logout(){
    this._service.logout();
    this._router.navigate([`login`]);
  }

}
