import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AppComponent } from './app.component';
import { ChildDataEntryComponent } from './child-data-entry/child-data-entry.component';
import { FileNotFound404Component } from './file-not-found404/file-not-found404.component'
import { LoginComponent } from './login/login.component';
import { SummarySheetComponent } from './summary-sheet/summary-sheet.component';
import { UnitlistComponent } from './unitlist/unitlist.component';
import { TrackingComponent } from './tracking/tracking.component';
import { CreateFormComponent } from './create-form/create-form.component';
import { AuthGuard } from "./_guards/auth.guard";
import { HpLoginComponent } from './hp-login/hp-login.component'
import { QuizComponent } from '././quiz/quiz/quiz.component'
import { PreviewComponent } from './admin/preview/preview.component'
import { AttendanceComponent } from './attendance/attendance.component'
import { LoginDirectComponent } from './login-direct/login-direct.component'
import { LoginIimpactComponent } from './login-iimpact/login-iimpact.component'

const routes: Routes = [
  {
    path: "admin",
    component: PreviewComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "data-entry/:state/:id",
    component: ChildDataEntryComponent,
    //canActivate: [AuthGuard],
  },
  {
    path: "attendance-data-entry/:state/:id",
    component: AttendanceComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "summary-sheet",
    component: SummarySheetComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "admin_create",
    component: CreateFormComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "",
    component: LoginIimpactComponent,
  },
  {
    path: "login",
    component: LoginIimpactComponent,
  },
  {
    path: "unitlist",
    component: UnitlistComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "tracking",
    component: TrackingComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "hp/login",
    component: HpLoginComponent
  },
  {
    path: "direct/login",
    component: LoginDirectComponent
  },
  {
    path: "quiz/:lang/:form_id",
    component: QuizComponent
  },
  {
    path        : '**',
    pathMatch   : 'full',
    component   : FileNotFound404Component
  }
];
// const routes: Routes = [
//   { path: "login", component: LoginComponent },
//   {
//     path: "",
//     component: UnitlistComponent,
//     canActivate: [AuthGuard],
//     children: [
//       {
//         path: "unitlist",
//         component: UnitlistComponent,
//       },
//       {
//         path: "data-entry/:id",
//         component: ChildDataEntryComponent,
//       }
//     ],
//   },
// ];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
