import { Component, OnInit, Input } from '@angular/core';

declare const $: any;

@Component({
  selector: 'app-custom-tabular',
  templateUrl: './custom-tabular.component.html',
  styleUrls: ['./custom-tabular.component.css']
})
export class CustomTabularComponent implements OnInit {

  constructor() { }

  @Input()
  tabularData:[];

  @Input()
  tableDetail:any
  lang;

  ngOnInit(): void {
    this.lang = localStorage.getItem('lang');
    console.log(this.lang);
    console.log(this.tabularData);
    console.log(this.tableDetail);
  }

}
