import { Component, OnInit, Input, OnChanges, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute } from '@angular/router'
import { ElementModel } from '../models/ElementModel'
import { ServerService } from '../_services/server.service'

declare const $: any

@Component({
  selector: 'app-custom-table',
  templateUrl: './custom-table.component.html',
  styleUrls: ['./custom-table.component.css']
})
export class CustomTableComponent implements OnInit, OnChanges {
  server: any;
  data: any;
  ServerService: any;
  form_id = '';

  constructor(private route: ActivatedRoute,private serverService: ServerService) {
    this.route.params.subscribe(event => {
      this.form_id = event.id
     });
   }

  @Input()
  tabularData:ElementModel;

  @Input()
  formData:any;

  @Input()
  reloadTable:number;

  @Output() sendFormData = new EventEmitter<any>();

  unitData = {};
  tabularHeader = [];
  responseData = [];
  //action_header:any = {param: "del", label: "Action"}; 

  ngOnInit(): void {
    let url =  this.tabularData.element_data['element_url']
    let parameters =  this.tabularData.element_data['element_parameters']
    //this.tabularData.element_data['element_header'].push(this.action_header);
    this.tabularHeader =  this.tabularData.element_data['element_header']
    console.log(this.tabularHeader);
    
    //this.unitData = this.formData;
    this.unitData['user_id'] = localStorage.getItem('user_id');

    //parameters['lc_code'] = this.unitData['user_id']
   // console.log(parameters);
  
    this.serverService.fetchTabularData_get(url,this.unitData['user_id'], this.form_id).then((response:[]) => {
      console.log(response['results']);
      
      this.responseData = response['results']
      $('#data_entry_data').DataTable().destroy();
      setTimeout(() => {
        $("#data_entry_data").DataTable({
          destroy:true,
          language: {
              paginate: {
                  previous: "<i class='mdi mdi-chevron-left'>",
                  next: "<i class='mdi mdi-chevron-right'>"
              }
          },
          aLengthMenu: [
            [10, 25, 50, 100, -1],
            ["All", 25, 50, 100]
        ],
          drawCallback: function() {
              $(".dataTables_paginate > .pagination").addClass("pagination-rounded")
          }
        });
      $('#data_entry_data').DataTable().draw();
      }, 500);
    }).catch((err)=>{
      console.log(err)
    })

    console.log('check reload : ',this.reloadTable)
  }
  isItemInfo(item) {
   let it = item.includes('_id')
    return it;
  }

  ngOnChanges(changes: SimpleChanges): void {
    // console.log('value change',changes.reloadTable.currentValue);
    // if(changes.reloadTable.currentValue!=changes.reloadTable.previousValue){
    //   console.log("reload check");
    //   this.ngOnInit();
    // }

    //$('#example').DataTable()
  }

  sendData(child_data){
    child_data['action']="update";
    console.log(child_data);
    localStorage.setItem('test_id',child_data.test_id)
    localStorage.setItem('child_id',child_data.child_id)
    localStorage.setItem('user_id',child_data.user_id)

    this.sendFormData.emit(child_data);
  }

  url(url: any, parameters: any[]) {
    throw new Error('Method not implemented.');
  }

}
